.dashboard-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 16px;
  }
  
  .dashboard-title {
    text-align: center;
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 1rem;
  }
  
  .tabs-list {
    display: flex;
    gap: 12px;
    justify-content: center;
    margin-bottom: 16px;
  }
  
  .tab-trigger {
    padding: 12px 20px;
    border-radius: 8px;
    font-weight: 600;
    background-color: #f3f4f6;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .tab-trigger[data-state='active'] {
    background-color: #2563eb;
    color: white;
  }
  
  .tab-trigger:hover {
    background-color: #cbd5e0;
  }
  
  .dropdown-container {
    display: flex;
    justify-content: center;
    margin-bottom: 16px;
  }
  
  .select-trigger {
    width: 200px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px;
    border: 1px solid #d1d5db;
    border-radius: 8px;
    background-color: white;
    cursor: pointer;
  }
  
  .select-content {
    background-color: white;
    border: 1px solid #d1d5db;
    border-radius: 8px;
    max-width: 200px;
    margin-top: 8px;
  }
  
  .chart-card {
    margin-top: 16px;
    border: 1px solid #e5e7eb;
    border-radius: 8px;
    padding: 16px;
  }
  
  .chart-content {
    display: flex;
    justify-content: center;
  }
  